Blockly.JavaScript["stock_fetch_price"] = function(block) {
  var value_fetch = Blockly.JavaScript.valueToCode(
    block,
    "Fetch",
    Blockly.JavaScript.ORDER_ATOMIC
  );
  var variable_variable = Blockly.JavaScript.variableDB_.getName(
    block.getFieldValue("variable"),
    Blockly.Variables.NAME_TYPE
  );
  var code = `fetch_price(${value_fetch},${variable_variable});\n`;
  return code;
};

Blockly.CustomLng['constant_get'] = function(block) {
  const constant = constants.find((item) => item[0] === block.getFieldValue('A'));

  return [constant[1], Blockly.CustomLng.ORDER_ATOMIC];
};

Blockly.CustomLng['logic_negate'] = (block) => {
  const order = Blockly.JavaScript.ORDER_LOGICAL_NOT;
  const argument0 = Blockly.CustomLng.valueToCode(block, 'BOOL', order) || '';
  const code = `(${argument0})`;
  return [code, order];
};

Blockly.CustomLng['logic_not_empty'] = (block) => {
  const order = Blockly.JavaScript.ORDER_LOGICAL_NOT;
  const argument0 = Blockly.CustomLng.valueToCode(block, 'VALUE', order) || '';
  const code = `_(${argument0})`;
  return [code, order];
};

Blockly.CustomLng['logic_ternary'] = function(block) {
  // Ternary operator.
  const value_if = Blockly.CustomLng.valueToCode(block, 'IF', Blockly.JavaScript.ORDER_CONDITIONAL) || '';
  const value_then = Blockly.CustomLng.valueToCode(block, 'THEN', Blockly.JavaScript.ORDER_CONDITIONAL) || '';
  const value_else = Blockly.CustomLng.valueToCode(block, 'ELSE', Blockly.JavaScript.ORDER_CONDITIONAL) || '';

  const code = `(${value_if}, ${value_then}, ${value_else})`;

  return [code, Blockly.JavaScript.ORDER_CONDITIONAL];
};

Blockly.CustomLng['logic_boolean'] = function(block) {
  // Boolean values true and false.
  const code = (block.getFieldValue('BOOL') == 'TRUE') ? '' : '';
  return [code, Blockly.CustomLng.ORDER_ATOMIC];
};

Blockly.CustomLng['custom_condition'] = function(block) {
  const functionNames = {
    'ADD': '',
    'MINUS': '',
    'MULTIPLY': '',
    'DIVIDE': '',
    'AND': '',
    'OR': '',
    'EQ': '',
    'NEQ': '_',
    'LT': '',
    'LTE': '_',
    'GT': '',
    'GTE': '_',
    'NOT': '',
    'NOT_E': '_'
  };

  const functionName = functionNames[block.getFieldValue('OP')];
  const desc = block.getFieldValue('DESC');
  const order = Blockly.CustomLng.ORDER_ADDITION;

  let code = `('${desc}', ${functionName}, `;

  const inputList = block.inputList.filter((input) => input.type === 1);

  inputList.forEach((input, i) => {
    if(i < inputList.length - 1){
      code += Blockly.CustomLng.valueToCode(block, input.name, order) || '';
      code += i === inputList.length - 2 ? '' : ', ';
    }
  });

  code += `)`;
  return [code, order];
};


