Blockly.Constants.Math.ADD_ARGUMENTS_MUTATOR_MIXIN = {
  mutationToDom: function() {
    const container = Blockly.utils.xml.createElement('mutation');
    container.setAttribute(`inputsLenght`, this.inputList.filter((input) => input.type === 1).length);

    return container;
  },
 
  domToMutation: function(xmlElement) {
    this.createShape_(xmlElement.getAttribute('inputsLenght'))
  },

  createShape_: function(inputsLenght) {
    for(let i = 0; i < inputsLenght; i++){
      const inputExists = this.getInput(`i_${i}`);

      if (!inputExists) {
        this.appendValueInput(`i_${i}`);
      }
    }
  },

  updateShape_: function() {
    const inputs = this.inputList.filter((input) => input.type === 1);

    const lastInputNotEmpty = inputs[inputs.length - 1].connection.targetBlock();
    const lastButOneInputNotEmpty = inputs[inputs.length - 2].connection.targetBlock();

    if(lastInputNotEmpty){
      this.appendValueInput(`i_${inputs.length}`)
    } else if (inputs.length > 2 && !lastInputNotEmpty && !lastButOneInputNotEmpty){
      this.removeInput(`i_${inputs.length - 1}`);
    }
  }
};

Blockly.Constants.Math.ADD_ARGUMENTS_MUTATOR_EXTENSION = function() {
  this.setOnChange(() => this.updateShape_());
};

Blockly.Extensions.registerMutator('custom_add_arguments_mutator',
    Blockly.Constants.Math.ADD_ARGUMENTS_MUTATOR_MIXIN,
    Blockly.Constants.Math.ADD_ARGUMENTS_MUTATOR_EXTENSION);



