'use strict';

import Blockly from 'blockly';


// CustomLng is dynamically typed.
Blockly.CustomLng['variables_get_dynamic'] =
    Blockly.CustomLng['variables_get'];
Blockly.CustomLng['variables_set_dynamic'] =
    Blockly.CustomLng['variables_set'];
