<template>
  <div id="app">
    <div class="addconst">
      <input type="text" ref="const_name" name="const_name" placeholder="" />
      <input type="text" ref="const_short_name" name="const_short_name" placeholder="  " />
      <button v-on:click="updateConsts()"> </button>
    </div>
    <div class="tabs">
      <div class="tabs__header">
        <ul class="tabs__buttons">
          <li v-on:click="openTab(1)" v-bind:class="{ tabs__button: true, active: openedTab === 1 }"> </li>
          <li v-on:click="openTab(2)" v-bind:class="{ tabs__button: true, active: openedTab === 2 }"> </li>
        </ul>
      </div>
      <div class="tabs__body">
        <div v-bind:style="{ opacity: openedTab === 1 ? '1' : '0', visibility: openedTab === 1 ? 'visible' : 'hidden' }" class="tabs__item">
          <textarea v-html="code" ref="code"></textarea>
        </div>
        <div v-bind:style="{ opacity: openedTab === 2 ? '1' : '0', visibility: openedTab === 2 ? 'visible' : 'hidden' }" class="tabs__item">
          <div id="blockly" class="blocklyDiv" ref="blocklyDiv"></div>
        </div>
      </div>
    </div>

    <button v-on:click="fromXML()"> </button>
    <button v-on:click="toXML()">  xml</button>

  </div>
</template>

<script>
