/**
 * @fileoverview Main Vue component that includes the Blockly component.
 * @author samelh@google.com (Sam El-Husseini)
 */

import './generators/customlng';
import './generators/customlng/colour';
import './generators/customlng/logic';
import './generators/customlng/loops';
import './generators/customlng/math';
import './generators/customlng/procedures';
import './generators/customlng/text';
import './generators/customlng/variables_dynamic';
import './generators/customlng/variables';
import './blocks/stocks';
import './prompt';

import {constants} from './blocks/stocks';

import Blockly from 'blockly';
import * as Ru from './langs/ru';

let acorn = require("acorn");

export default {
  name: 'app',
  data(){
    return {
      constants: [],
      workspace: null,
      openedTab: 1,
      code: '',
      options: {
        media: 'media/',
        grid:
          {
            spacing: 25,
            length: 3,
            colour: '#ccc',
            snap: true
          },
        toolbox:
        `<xml>
        <category name="" colour="%{BKY_MATH_HUE}">
          <block type="variables_set">
            <field name="VAR"></field>
          </block>
          <block type="math_number">
            <field name="NUM">123</field>
          </block>
          <block type="custom_arithmetic"></block>
          <block type="custom_condition"></block>
          <block type="custom_logic_operation"></block>
          <block type="logic_compare"></block>
          <block type="logic_ternary"></block>
          <block type="logic_negate"></block>
          <block type="logic_not_empty"></block>
          <block type="logic_boolean"></block>
        </category>
        <category name="" custom="VARIABLE" colour="%{BKY_VARIABLES_HUE}">
          <block type="variables_get">
              <field name="VAR">sss</field>
          </block>
        </category>
        <category name="" colour="%{BKY_VARIABLES_HUE}" custom="CONSTANTS"></category>
        </xml>`
      },
      xml: ''
    }
  },
  mounted() {
    var options = this.options || {};
    if (!options.toolbox) {
      options.toolbox = this.$refs["blocklyToolbox"];
    }
    Blockly.setLocale(Ru);
    this.workspace = Blockly.inject(this.$refs["blocklyDiv"], options);
    this.workspace.registerToolboxCategoryCallback(
    'CONSTANTS', this.coloursFlyoutCallback)
  },
  methods: {
    coloursFlyoutCallback () {
      var xmlList = [];
      if (Blockly.Blocks['constant_get']) {
        for (var i = 0; i < constants.length; i++) {
          var blockText = '<block type="constant_get">' +
              '<field name="A">' + constants[i][0] + '</field>' +
              '</block>';
          var block = Blockly.Xml.textToDom(blockText);
          xmlList.push(block);
        }
      }
      return xmlList;
    },
    updateConsts(){
      constants.push([this.$refs["const_name"].value, this.$refs["const_short_name"].value]);
      this.workspace.toolbox_.refreshSelection();
      this.$refs["const_name"].value = '';
      this.$refs["const_short_name"].value = '';
    },
    openTab(tab){
      this.openedTab = tab; 
      if(tab === 1) this.showCode();
    },
    showCode() {
      this.$refs["code"].value = Blockly.CustomLng.workspaceToCode(this.workspace);
    },
    toXML() {
      var xmlDom = Blockly.Xml.workspaceToDom(Blockly.mainWorkspace);
      var xmlText = Blockly.Xml.domToPrettyText(xmlDom);
      console.log(xmlText)
    },
    getArguments(arr) {
      arr.forEach((node, i) => {
        this.xml += `<value name="${i === 0 ? 'A' : 'B'}">`;
        if(node.type === 'CallExpression'){
          this.xml += `<block type="math_arithmetic"><field name="OP">DIVIDE</field>`;
          this.getArguments(node.arguments);
          this.xml += `</block>`;
        }else{
          this.xml += `<block type="math_number"><field name="NUM">${node.value}</field></block>`
        }
        this.xml += `</value>`;
      });
    },
    fromXML() {
      //const code = `((1, 1), (1, 1))`;\
      const code = this.$refs["code"].value;

      const codeObj = acorn.parse(code, {ecmaVersion: 2020}).body;

      this.xml = `<xml xmlns="https://developers.google.com/blockly/xml">`;

      for (let key in codeObj) {
        //console.log(codeObj[key]);
        const express = codeObj[key];
        if(express.type === 'ExpressionStatement'){
          this.xml += `<block type="math_arithmetic"><field name="OP">DIVIDE</field>`;
          this.getArguments(express.expression.arguments);
          this.xml += `</block>`;
        }
      }

      this.xml += `</xml>`;
      //console.log(this.xml)     

      /*const xml2 = `<xml xmlns="https://developers.google.com/blockly/xml">
  <variables>
    <variable>a</variable>
    <variable>b</variable>
  </variables>
  <block type="variables_set">
    <field name="VAR">a</field>
    <value name="VALUE">
      <block type="math_number">
        <field name="NUM">123</field>
      </block>
    </value>
    <next>
      <block type="variables_set">
        <field name="VAR">b</field>
        <value name="VALUE">
          <block type="logic_compare">
            <field name="OP">EQ</field>
            <value name="A">
              <block type="math_arithmetic">
                <field name="OP">ADD</field>
                <value name="A">
                  <block type="variables_get">
                    <field name="VAR">a</field>
                  </block>
                </value>
              </block>
            </value>
          </block>
        </value>
      </block>
    </next>
  </block>
</xml>`;*/

      if (typeof this.xml != "string" || this.xml.length < 5) {
          return false;
      }
      try {
          var dom = Blockly.Xml.textToDom(this.xml);
          Blockly.mainWorkspace.clear();
          Blockly.Xml.domToWorkspace(dom, Blockly.mainWorkspace);
          return true;
      } catch (e) {
          return false;
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding: 10px;
}

html, body {
  margin: 0;
}

.tabs__buttons {
    list-style: none;
    margin: 0;
    padding: 0;
}

.tabs__button {
    display: inline-block;
    padding: 10px;
    cursor: pointer;
    margin: 0 10px;
}

.tabs__button.active {
    font-weight: bold;
}

.tabs__body{
  position: relative;
  width: 100%;
  height: 700px;
}

.tabs__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.tabs__item textarea{
  width: 100%;
  height: 100%;
}

#blockly{
  width: 100%;
  height: 700px;
}

</style>
