/**
 * @fileoverview Generating CustomLng for logic blocks.
 * @author q.neutron@gmail.com (Quynh Neutron)
 */
'use strict';

import Blockly from 'blockly';

Blockly.CustomLng['controls_if'] = function(block) {
  // If/elseif/else condition.
  var n = 0;
  var code = '', branchCode, conditionCode;
  if (Blockly.CustomLng.STATEMENT_PREFIX) {
    // Automatic prefix insertion is switched off for this block.  Add manually.
    code += Blockly.CustomLng.injectId(Blockly.CustomLng.STATEMENT_PREFIX,
        block);
  }
  do {
    conditionCode = Blockly.CustomLng.valueToCode(block, 'IF' + n,
        Blockly.CustomLng.ORDER_NONE) || 'false';
    branchCode = Blockly.CustomLng.statementToCode(block, 'DO' + n);
    if (Blockly.CustomLng.STATEMENT_SUFFIX) {
      branchCode = Blockly.CustomLng.prefixLines(
          Blockly.CustomLng.injectId(Blockly.CustomLng.STATEMENT_SUFFIX,
          block), Blockly.CustomLng.INDENT) + branchCode;
    }
    code += (n > 0 ? ' else ' : '') +
        'if (' + conditionCode + ') {\n' + branchCode + '}';
    ++n;
  } while (block.getInput('IF' + n));

  if (block.getInput('ELSE') || Blockly.CustomLng.STATEMENT_SUFFIX) {
    branchCode = Blockly.CustomLng.statementToCode(block, 'ELSE');
    if (Blockly.CustomLng.STATEMENT_SUFFIX) {
      branchCode = Blockly.CustomLng.prefixLines(
          Blockly.CustomLng.injectId(Blockly.CustomLng.STATEMENT_SUFFIX,
          block), Blockly.CustomLng.INDENT) + branchCode;
    }
    code += ' else {\n' + branchCode + '}';
  }
  return code + '\n';
};

Blockly.CustomLng['controls_ifelse'] = Blockly.CustomLng['controls_if'];

Blockly.CustomLng['logic_compare'] = function(block) {
  // Comparison operator.
  var OPERATORS = {
    'EQ': '==',
    'NEQ': '!=',
    'LT': '<',
    'LTE': '<=',
    'GT': '>',
    'GTE': '>='
  };
  var operator = OPERATORS[block.getFieldValue('OP')];
  var order = (operator == '==' || operator == '!=') ?
      Blockly.CustomLng.ORDER_ADDITION : Blockly.CustomLng.ORDER_ADDITION;
  
  const functionNames = {
    '==': '',
    '!=': '_',
    '<': '',
    '<=': '_',
    '>': '',
    '>=': '_'
  }

  var argument0 = Blockly.CustomLng.valueToCode(block, 'A', order) || '0';
  var argument1 = Blockly.CustomLng.valueToCode(block, 'B', order) || '0';

  const code = `${functionNames[operator] || ''}(${argument0}, ${argument1})`;

  return [code, order];
};

Blockly.CustomLng['logic_operation'] = function(block) {
  // Operations 'and', 'or'.
  var operator = (block.getFieldValue('OP') == 'AND') ? '&&' : '||';
  var order = (operator == '&&') ? Blockly.CustomLng.ORDER_LOGICAL_AND :
      Blockly.CustomLng.ORDER_LOGICAL_OR;
  var argument0 = Blockly.CustomLng.valueToCode(block, 'A', order);
  var argument1 = Blockly.CustomLng.valueToCode(block, 'B', order);
  if (!argument0 && !argument1) {
    // If there are no arguments, then the return value is false.
    argument0 = 'false';
    argument1 = 'false';
  } else {
    // Single missing arguments have no effect on the return value.
    var defaultArgument = (operator == '&&') ? 'true' : 'false';
    if (!argument0) {
      argument0 = defaultArgument;
    }
    if (!argument1) {
      argument1 = defaultArgument;
    }
  }
  const code = `${operator == '&&' ? ''  : '' }(${argument0}, ${argument1})`;
  return [code, order];
};

Blockly.CustomLng['logic_negate'] = function(block) {
  // Negation.
  var order = Blockly.CustomLng.ORDER_LOGICAL_NOT;
  var argument0 = Blockly.CustomLng.valueToCode(block, 'BOOL', order) ||
      'true';
  var code = '!' + argument0;
  return [code, order];
};

Blockly.CustomLng['logic_boolean'] = function(block) {
  // Boolean values true and false.
  var code = (block.getFieldValue('BOOL') == 'TRUE') ? 'true' : 'false';
  return [code, Blockly.CustomLng.ORDER_ATOMIC];
};

Blockly.CustomLng['logic_null'] = function() {
  // Null data type.
  return ['null', Blockly.CustomLng.ORDER_ATOMIC];
};

Blockly.CustomLng['logic_ternary'] = function(block) {
  // Ternary operator.
  var value_if = Blockly.CustomLng.valueToCode(block, 'IF',
      Blockly.CustomLng.ORDER_CONDITIONAL) || 'false';
  var value_then = Blockly.CustomLng.valueToCode(block, 'THEN',
      Blockly.CustomLng.ORDER_CONDITIONAL) || 'null';
  var value_else = Blockly.CustomLng.valueToCode(block, 'ELSE',
      Blockly.CustomLng.ORDER_CONDITIONAL) || 'null';
  var code = value_if + ' ? ' + value_then + ' : ' + value_else;
  return [code, Blockly.CustomLng.ORDER_CONDITIONAL];
};
