/**
 * @fileoverview Generating CustomLng for variable blocks.
 * @author fraser@google.com (Neil Fraser)
 */
'use strict';

import Blockly from 'blockly';


Blockly.CustomLng['variables_get'] = function(block) {
  // Variable getter.
  var code = Blockly.CustomLng.variableDB_.getName(block.getFieldValue('VAR'),
      Blockly.VARIABLE_CATEGORY_NAME);
  return [code, Blockly.CustomLng.ORDER_ATOMIC];
};

Blockly.CustomLng['variables_set'] = function(block) {
  // Variable setter.
  var argument0 = Blockly.CustomLng.valueToCode(block, 'VALUE',
      Blockly.CustomLng.ORDER_ASSIGNMENT) || '';
  var varName = Blockly.CustomLng.variableDB_.getName(
      block.getFieldValue('VAR'), Blockly.VARIABLE_CATEGORY_NAME);
  return varName + ' = ' + argument0 + '\n';
};
